'use client';
import UploadPhoto from '@/components/ai-analysis/UploadPhoto';
import { useMutation } from '@tanstack/react-query';
import axios from 'axios';
import { useState } from 'react';

const Page = () => {
    const [fileName, setFileName] = useState('');
    const [file, setFile] = useState<File | null>(null);


    const [isAnalyzed, setIsAnalyzed] = useState<boolean>(false);
    const sendPhoto = async (file: File) => {
        const formData = new FormData();
        formData.append("file", file);
        const res = await axios.post("...", formData, {
            withCredentials: true,
            headers: { "Content-Type": "multipart/form-data" }
        });
        return res.data;
    }


    const { error, mutate, isPending, data: analyzedInfo } = useMutation({
        mutationFn: sendPhoto,
        onSuccess: () => {
            setIsAnalyzed(true);
        },

    })
    return (
        isAnalyzed ?
            <UploadPhoto isPending={isPending} file={file} fileName={fileName} setFile={setFile} mutate={mutate} setFileName={setFileName}/>
            :
            <AnalyzedResults />
    );
}
export default Page;